/**
 * Newurl model events
 */

'use strict';

import {EventEmitter} from 'events';
var Newurl = require('./newurl.model');
var NewurlEvents = new EventEmitter();

// Set max event listeners (0 == unlimited)
NewurlEvents.setMaxListeners(0);

// Model events
var events = {
  'save': 'save',
  'remove': 'remove'
};

// Register the event emitter to the model events
for (var e in events) {
  var event = events[e];
  Newurl.schema.post(e, emitEvent(event));
}

function emitEvent(event) {
  return function(doc) {
    NewurlEvents.emit(event + ':' + doc._id, doc);
    NewurlEvents.emit(event, doc);
  }
}

export default NewurlEvents;
